@font-face {
  font-family: "ptmono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "robotocondensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "robotocondensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "ptmono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: -webkit-min-content, 1fr, -webkit-min-content;
  grid-template-rows: min-content, 1fr, min-content;
  min-height: 100vh;
  min-width: 320px;
  background-color: #ffffff;
  color: #666666; }

header {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  width: 100%;
  background-color: transparent; }
  @media (min-width: 1024px) {
    header {
      padding: calc((100% - 73.4895%) / 2);
      padding-top: 33px;
      padding-bottom: 10px; } }
  @media (max-width: 1023px) {
    header {
      padding: calc((100% - 93.5065%) / 2);
      padding-top: 20px;
      padding-bottom: 10px; } }
  @media (max-width: 767px) {
    header {
      padding: calc((100% - 90.625%) / 2);
      padding-top: 17px;
      padding-bottom: 10px; } }

main {
  height: 100px;
  background-color: #000; }

img {
  display: block; }

.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 1920px; }

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden; }

.modal-open {
  overflow: hidden; }

.list {
  margin: 0;
  padding: 0;
  list-style: none; }

.button {
  border: none;
  outline: none; }

.link,
.button {
  cursor: pointer; }

.logo {
  padding-right: 100px;
  margin-right: auto; }
  @media (max-width: 767px) {
    .logo img {
      width: 38px;
      height: 38px; } }

@media (min-width: 1024px) {
  .contacts {
    margin-right: 2.7%;
    padding-right: 10px;
    color: #ffffff; } }

.contacts__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content; }
  @media (min-width: 1024px) {
